<template>
  <div class="container">
    <div class="row">

      <div v-for="(product) in products.slice(1,6)" :key="product.price" class="mx-4 my-2"  >
        <b-card body-class="col"
                :title=" product.attributes.name.no"
                img-src="https://picsum.photos/600/300/?image=25"
                img-alt="Image"
                img-top
                tag="article"
                style="max-width: 20rem;"
                class="mb-2"
        >
          <b-card-text>
            <h3 >
              {{ product.attributes.cost}}
            </h3>
            <span v-html="substrHtmlProd(product.attributes.description.no)">
            </span>
          </b-card-text>

          <b-button @click="addToCard(product)" variant="primary">Go somewhere</b-button>
        </b-card>
      </div>

    </div>
  </div>
</template>

